@media only screen and (max-width:4000px){
/* MATERIAL-UI DESIGN */
/* https://material.io/tools/icons/?icon=description&style=outline */

.App {
  display: block;
  text-align: center;  
  padding: 2%;
}

.MuiButton-label:after{
  background: red;
}

#navbar{
  font-family: 'Times New Roman', Times, serif;
  position: fixed;
  text-align: left;  
  padding: 1% 0% 1% 1%;
  /* background: rgba(255, 255, 255, 0.849); */
  /* color: rgb(91, 133, 224); */
  color: white;
  font-size: 1.2em;
  z-index: 1;
  width: 100%;
  
}

#intro{
  font-size: x-large;
  margin: 15% 20% 20% 20%;
}

table{
  margin: 0 auto;
  margin-top: 2%;
  text-align: left;
  font-size: xx-large;
}

.material-icons.md-48 {
  font-family: 'Material Icons';
  font-weight: normal;
  font-style: normal;
  font-size: 48px;
}

#userFormSection{
  display: grid;
  grid-template-columns: 50% 50%;
  grid-template-rows: 10% 90%;
  justify-items: center;
  margin: 0px;
  padding: 0px;
  grid-gap: 0px;
}

/* #userForm{
  grid-column-start: 1;
  grid-column-end: 3;
} */

img{
  /* background: url('./Infographic/infographic.JPG'); */
  width: 80%;
  height: 900px;
  background-repeat: no-repeat;
  background-size: cover;
}


#userForm{
  background: rgba(255, 255, 255, 0.404);
  width: 75%;
  margin: 0 auto;
  margin: 0% auto 20% auto;
  padding: 2% 2% 2% 2%;
  font-size: 1.5em;
  display: grid;
  grid-template-columns: 30% 70%;
  grid-gap: 3%;
  border-radius: 5px;
  justify-items: center;
}

#form-primer{
  grid-column-start: 1;
  grid-column-end: 2;
}

.formLabels{
  text-align: left;
  border-right: 1px solid gray;
  grid-column-start: 2;
  grid-column-end: 3;
  padding-right: 10%;
}

.formInputs{
  text-align: left;
  grid-column-start: 2;
  grid-column-end: 4;
  line-height: 1.5;
  padding: 2%;
}

span{
  width: 20px;
  height: 20px;
}

button{
  margin: 0%;
  width: 50%;
}

label{
  font-size: 1.2em;
}

input, select {
  width: 100%;
  font-size: 1em;
  background: rgba(65, 99, 173, 0.541);
  color: white;
  outline: none;
  border: none;
  border-radius: 5px;
  padding: 2%;
  /* border-bottom: 1px solid white; */
  /* border: none; */
}

#checkboxes{
  display: flex;
  flex-direction: row;
  justify-content: space-around;

}

}

